.airdrop-container {
  display: block;
  position: relative;
  z-index: 10;
  max-width: 1000px;
  min-height: calc(100vh - 75px);
  margin: 0 auto; }
  @media (min-width: 992px) {
    .airdrop-container {
      min-height: calc(100vh - 125px); } }
  .airdrop-container .airdrop-title {
    display: block;
    position: relative; }
    .airdrop-container .airdrop-title .title-text {
      display: block;
      width: 100%;
      text-align: center;
      font-size: 28px;
      font-weight: 500;
      color: white;
      letter-spacing: 1px; }
      @media (min-width: 992px) {
        .airdrop-container .airdrop-title .title-text {
          font-size: 32px;
          margin: 50px 0 25px 0; } }
    .airdrop-container .airdrop-title .connect-button {
      display: block;
      position: relative;
      z-index: 10;
      width: 165px;
      text-align: center;
      font-size: 12px;
      font-weight: 600;
      margin: 25px auto;
      color: #000000;
      border: none;
      border-radius: 0.25rem;
      padding: 10px 8px;
      box-shadow: 0 1px 2px rgba(255, 255, 255, 0.07), 0 2px 4px rgba(255, 255, 255, 0.07), 0 4px 8px rgba(255, 255, 255, 0.07), 0 8px 16px rgba(255, 255, 255, 0.07), 0 16px 32px rgba(255, 255, 255, 0.07), 0 32px 64px rgba(255, 255, 255, 0.07);
      cursor: pointer; }
      .airdrop-container .airdrop-title .connect-button:hover {
        color: #999999; }
  .airdrop-container .airdrop-subtitle {
    display: block;
    width: 100%;
    text-align: center;
    font-size: 16px;
    font-weight: 600;
    color: white;
    letter-spacing: 1px;
    margin: 10px 0; }
    .airdrop-container .airdrop-subtitle span {
      font-weight: 300;
      opacity: 0.75; }
  .airdrop-container .airdrop-details {
    display: block;
    margin: 45px auto;
    text-align: center;
    position: relative;
    border-radius: 0.75rem;
    overflow: hidden; }
    @media (min-width: 576px) {
      .airdrop-container .airdrop-details {
        max-width: 450px;
        margin-top: 75px; } }
    @media (min-width: 768px) {
      .airdrop-container .airdrop-details {
        margin-top: 100px; } }
    @media (min-width: 992px) {
      .airdrop-container .airdrop-details {
        max-width: 750px; } }
    .airdrop-container .airdrop-details .upper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 22px;
      background-color: #531818; }
      @media (min-width: 992px) {
        .airdrop-container .airdrop-details .upper {
          padding: 44px 66px; } }
      .airdrop-container .airdrop-details .upper .details-item .title {
        font-size: 13px;
        color: white;
        margin-bottom: 8px;
        opacity: 0.75; }
        @media (min-width: 992px) {
          .airdrop-container .airdrop-details .upper .details-item .title {
            font-size: 16px; } }
      .airdrop-container .airdrop-details .upper .details-item .value {
        font-size: 16px;
        color: white;
        letter-spacing: 1px; }
        @media (min-width: 992px) {
          .airdrop-container .airdrop-details .upper .details-item .value {
            font-size: 28px; } }
    .airdrop-container .airdrop-details .lower {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      padding: 25px 0;
      background-color: #864d4d; }
      @media (min-width: 992px) {
        .airdrop-container .airdrop-details .lower {
          padding: 50px 0; } }
      .airdrop-container .airdrop-details .lower .claim-item {
        display: block;
        width: 100%; }
        .airdrop-container .airdrop-details .lower .claim-item .title {
          font-size: 20px;
          color: white;
          margin-bottom: 8px;
          opacity: 0.75;
          letter-spacing: 1px; }
          @media (min-width: 992px) {
            .airdrop-container .airdrop-details .lower .claim-item .title {
              font-size: 28px; } }
        .airdrop-container .airdrop-details .lower .claim-item .value {
          font-size: 28px;
          font-weight: 500;
          color: #9afa9a;
          letter-spacing: 1px; }
          @media (min-width: 992px) {
            .airdrop-container .airdrop-details .lower .claim-item .value {
              font-size: 36px; } }
      .airdrop-container .airdrop-details .lower .claim-btn {
        display: block;
        padding: 12px 20px;
        border: none;
        background-color: white;
