        margin: 25px auto 0 auto;
        cursor: pointer; }
        @media (min-width: 992px) {
          .airdrop-container .airdrop-details .lower .claim-btn {
            padding: 12px 32px; } }
        .airdrop-container .airdrop-details .lower .claim-btn:hover {
          color: #999999; }
      .airdrop-container .airdrop-details .lower .claim-disconnected {
        display: block;
        text-align: center;
